<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <Navigation v-if="currentUser"></Navigation>
    <router-view/>
  </div>
</template>

<script>
  import {
    mapState
  } from 'vuex'
  import Navigation from './components/Navigation'

  export default {
    name: 'App',
    components: {
      Navigation
    },
    // computed计算属性，实时计算读取变量变化
    // mapState通过扩展运算符将store.state.currentUser 映射this.currentUser 这个this 很重要，
    // 这个映射直接映射到当前Vue的this对象上。
    computed: {
      ...mapState(['currentUser'])
    }
  }

</script>

<style>
  /* #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
  } */

</style>



