import Vue from 'vue'
import Vuex from 'vuex'
import {
    debug
} from 'util';

Vue.use(Vuex)

const fb = require('./components/firebaseConfig.js')
    // handle page reload
fb.auth.onAuthStateChanged(user => {
    if (user) {
        store.commit('setCurrentUser', user)
        store.dispatch('fetchUserProfile')

        // console.log('当前登录的user.uid =>', user.uid)

        fb.usersCollection.doc(user.uid).onSnapshot(doc => {
            store.commit('setUserProfile', doc.data());
        })

        let refData = fb.postsCollection.orderBy('createOn', 'desc')

        // console.log('获取当前列表数据=>');
        // refData.get().then(data => {
        //     data.forEach(doc => {
        //         console.log(doc.id, "=>", doc.data())
        //     })
        // })

        // refData.onSnapshot(function(doc) {
        //     var source = doc.metadata.hasPendingWrites ? "Local" : "Server";
        //     console.log("文档快照 =》 ", source, " data: ", doc.docs);

        //     let docChangesArr = doc.docChanges();
        //     console.log('docChangesArr', docChangesArr)
        //     docChangesArr.forEach(change => {
        //         console.log('changeType', change.type);
        //         console.log('changeData', change.doc.data().userId)
        //     })
        // });


        // realtime updates from our posts collection
        refData.onSnapshot(querySnapshot => {
            // check if created by currentUser

            let createdByCurrentUser
            let queryDocChangesArr = querySnapshot.docChanges();

            if (querySnapshot.docs.length) {
                createdByCurrentUser = store.state.currentUser.uid == queryDocChangesArr[0].doc.data().userId ? true : false
            }

            // add new posts to hiddenPosts array after initial load
            if (queryDocChangesArr.length !== querySnapshot.docs.length &&
                queryDocChangesArr[0].type == 'added' && !createdByCurrentUser) {

                let post = queryDocChangesArr[0].doc.data()
                post.id = queryDocChangesArr[0].doc.id

                store.commit('setHiddenPosts', post)
            } else {
                let postsArray = []

                querySnapshot.forEach(doc => {
                    let post = doc.data()
                    post.id = doc.id
                    postsArray.push(post)
                })

                store.commit('setPosts', postsArray)
            }
        })
    }
})

// 这里是 输出 store 这个变量, import 时也要对应的进入导入。或者export default，import时就可以自定义名字
export const store = new Vuex.Store({
    //    设置状态对象
    state: {
        currentUser: null,
        userProfile: {},
        posts: [],
        hiddenPosts: []
    },
    // 异步处理数据 dispath，Action 提交的是 mutation，而不是直接变更状态。
    actions: {
        fetchUserProfile({
            commit,
            state
        }) {
            fb.usersCollection.doc(state.currentUser.uid).get().then(res => {
                // console.log('userProfile的数据 = 》 ', res.data())
                commit('setUserProfile', res.data())

            }).catch(err => {
                console.log(err)
            })
        },
        clearData({
            commit
        }) {
            commit('setCurrentUser', null);
            commit('setUserProfile', null);
            commit('setPosts', null);
        },
        updateProfile({
            commit,
            state
        }, data) {
            let name = data.name;
            let title = data.title;

            fb.usersCollection.doc(state.currentUser.uid).update({
                name,
                title
            }).then(user => {
                //update all posts by user to reflect new name
                fb.postsCollection.where('userId', '==', state.currentUser.uid).get().then(docs => {
                        docs.forEach(doc => {
                            fb.postsCollection.doc(doc.id).update({
                                userName: name
                            })
                        })
                    })
                    // update all comments by user to reflect new name
                fb.commentsCollection.where('userId', '==', state.currentUser.uid).get().then(docs => {
                    docs.forEach(doc => {
                        fb.commentsCollection.doc(doc.id).update({
                            userName: name
                        })
                    })
                })
            }).catch(err => {
                console.log(err)
            })
        }
    },
    // 同步处理数据 commit
    mutations: {
        setCurrentUser(state, val) {
            state.currentUser = val
        },
        setUserProfile(state, val) {
            state.userProfile = val
        },
        setPosts(state, val) {
            if (val) {
                state.posts = val

            } else {
                state.posts = []
            }
        },
        setHiddenPosts(state, val) {
            if (val) {
                // make sure not to add duplicates
                if (!state.hiddenPosts.some(x => x.id === val.id)) {
                    state.hiddenPosts.unshift(val);
                }
            } else {
                state.hiddenPosts = []
                    // console.log('点击后有没有执行到这里=====>', val)
            }
        }
    }
})


