<template>
  <div id="login">
    <transition name="fade">
      <div v-if="performingRequest" class="loading">
        <p>Loading...</p>
      </div>
    </transition>
    <section>
      <div class="col1">
        <h1>Alibazar BMS</h1>
        <p>营业收支日志|产品库存记录</p>
      </div>
      <div class="col2" :class="{ 'signup-form': !showLoginForm && !showForgotPassword }">
        <!-- Login开始 -->
        <!-- @submit.prevent 提交不刷新页面 , @ 为 v-on 缩写， : 为 v-bind缩写 -->
        <form v-if="showLoginForm" @submit.prevent>
          <h1>Welcome Back</h1>

          <label for="email1">Email</label>
          <!-- v-model.trim 自动过滤用户输入的首尾空白字符，可以给 v-model 添加 trim 修饰符-->
          <input v-model.trim="loginForm.email" type="text" placeholder="you@email.com" id="email1" />

          <label for="password1">Password</label>
          <input v-model.trim="loginForm.password" type="password" placeholder="******" id="password1" />

          <button @click="login" class="button">Log In</button>

          <div class="extras">
            <a @click="togglePasswordReset">Forgot Password</a>
            <a @click="toggleForm">Create an Account</a>
          </div>
        </form>
        <!-- Login 结束 -->
        <!-- Register 开始 -->
        <form v-if="!showLoginForm && !showForgotPassword" @submit.prevent>
          <h1>Get Started</h1>

          <label for="name">Name</label>
          <input v-model.trim="signupForm.name" type="text" placeholder="Savvy Apps" id="name" />

          <label for="title">Title</label>
          <input v-model.trim="signupForm.title" type="text" placeholder="Company" id="title" />

          <label for="email2">Email</label>
          <input v-model.trim="signupForm.email" type="text" placeholder="you@email.com" id="email2" />

          <label for="password2">Password</label>
          <input v-model.trim="signupForm.password" type="password" placeholder="min 6 characters" id="password2" />

          <button @click="signup" class="button">Sign Up</button>

          <div class="extras">
            <a @click="toggleForm">Back to Log In</a>
          </div>
        </form>
        <!-- Register 结束 -->
        <!-- showForgotPassword 开始 -->
        <form v-if="showForgotPassword" @submit.prevent class="password-reset">
          <div v-if="!passwordResetSuccess">
            <h1>Reset Password</h1>
            <p>We will send you an email to reset your password</p>

            <label for="email3">Email</label>
            <input v-model.trim="passwordForm.email" type="text" placeholder="you@email.com" id="email3" />

            <button @click="resetPassword" class="button">Submit</button>

            <div class="extras">
              <a @click="togglePasswordReset">Back to Log In</a>
            </div>
          </div>
          <div v-else>
            <h1>Email Sent</h1>
            <p>check your email for a link to reset your password</p>
            <button @click="togglePasswordReset" class="button">Back to login</button>
          </div>
        </form>
        <!-- showForgotPassword 结束 -->
        <transition name="fade">
          <div v-if="errorMsg !== ''" class="error-msg">
            <p>{{ errorMsg }}</p>
          </div>
        </transition>
      </div>
    </section>
  </div>
</template>

<script>
  const fb = require('./firebaseConfig.js')

  export default {
    name: 'Login',
    data() {
      return {
        loginForm: {
          email: '',
          password: ''
        },
        signupForm: {
          name: '',
          title: '',
          email: '',
          password: ''
        },
        passwordForm: {
          email: ''
        },
        showLoginForm: true,
        showForgotPassword: false,
        passwordResetSuccess: false,
        performingRequest: false,
        errorMsg: ''
      }
    },
    methods: {
      toggleForm() {
        this.errorMsg = ''
        this.showLoginForm = !this.showLoginForm
      },
      togglePasswordReset() {
        if (this.showForgotPassword) {
          this.showLoginForm = true
          this.showForgotPassword = false
          this.passwordResetSuccess = false
        } else {
          this.showLoginForm = false
          this.showForgotPassword = true
        }
      },
      resetPassword() {
        this.performingRequest = true
        fb.auth.sendPasswordResetEmail(this.passwordForm.email).then(() => {
          this.performingRequest = false
          this.passwordResetSuccess = true
          this.passwordForm.email = ''
        }).catch(err => {
          console.log(err)
          this.performingRequest = false
          this.errorMsg = err.message
        })
      },

      login() {
        this.performingRequest = true;

        fb.auth.signInWithEmailAndPassword(this.loginForm.email, this.loginForm.password).then(user => {

          if (user.user) {
            // 响应store里面的 mutations，同步
            // console.log('登录后获取user信息=>', user.user, user.user.displayName);


            this.$store.commit('setCurrentUser',
              user.user);
            // 响应store里面的 actions， 异步
            this.$store.dispatch('fetchUserProfile');
            this.performingRequest = false
            // 跳转到 首页
            this.$router.push('/dashboard')
          } else {
            console.log('当前登录的Email出现未知错误')
          }

        }).catch(err => {
          console.log(err.code, err.message);
          this.performingRequest = false;
          this.errorMsg = err.message;

          switch (err.code) {
            case 'auth/user-not-found':
              console.log('当前Email还未注册或已被删除，请先进行注册！');
              this.errorMsg = '当前Email还未注册或已被删除，请先进行注册！';
              this.performingRequest = false;
              this.showLoginForm = false;
              this.$router.push('/login');
              break;

            default:
              break;
          }

        })
      },
      signup() {
        this.performingRequest = true;
        fb.auth.createUserWithEmailAndPassword(this.signupForm.email, this.signupForm.password).then(user => {

          let cUser = fb.auth.currentUser
          user = cUser;
          this.$store.commit('setCurrentUser',
            cUser)
          console.log("创建帐号成功！！", cUser.uid)

          // create user obj 创建用户数据库并写入到user集合
          fb.usersCollection.doc(cUser.uid).set({
            name: this.signupForm.name,
            title: this.signupForm.title
          }).then(() => {
            this.$store.dispatch('fetchUserProfile');
            this.performingRequest = false;
            this.$router.push('/dashboard');
          }).catch(err => {
            console.log(err);
            this.performingRequest = false;
            this.errorMsg = err.message;
          })
        }).catch(err => {
          console.log(err);
          this.performingRequest = false;
          this.errorMsg = err.message;
        })
      },
      toggleForm() {
        this.showLoginForm = !this.showLoginForm
      }
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



