<template>
  <div id="dashboard">
    <section>
      <div class="col1">
        <div class="profile">
          <h5>{{ userProfile.name }}</h5>
          <p>{{ userProfile.title }}</p>
          <div class="create-post">
            <p>create a post</p>
            <form @submit.prevent>
              <!-- <textarea v-model.trim="post.content"></textarea> -->
              <input type="text" v-model.trim="post.content">
              <!-- :disabled 这里用来v-bind判断当前没有任何输入，按钮变灰，不能够post -->
              <button @click="createPost" :disabled="post.content==''" class="button">post</button>
            </form>
          </div>
        </div>
      </div>
      <div class="col2">
        <!-- 右侧 内容列表 -->
        <!-- 判断显示 非当前用户的新帖子 -->
        <transition name="fade">
          <div v-if="hiddenPosts.length" @click="showNewPosts" class="hidden-posts">
            <p>
              Click to show
              <span class="new-posts">{{ hiddenPosts.length }}</span>
              new
              <span v-if="hiddenPosts.length > 1">posts</span>
              <span v-else>post</span>
            </p>
          </div>
        </transition>

        <!-- comment modal -->
        <transition name="fade">
          <div v-if="showCommentModal" class="c-modal">
            <div class="c-container">
              <a @click="closeCommentModal">X</a>
              <p>add a comment</p>
              <form @submit.prevent>
                <textarea v-model.trim="comment.content"></textarea>
                <button @click="addComment" :disabled="comment.content == ''" class="button">add comment</button>
              </form>
            </div>
          </div>
        </transition>

        <!-- full post modal -->
        <transition name="fade">
          <div v-if="showPostModal" class="p-modal">
            <div class="p-container">
              <a @click="closePostModal" class="close">X</a>
              <div class="post">
                <h5>{{fullPost.userName}}</h5>
                <span>{{fullPost.createOn | formatDate}}</span>
                <p>{{fullPost.content}}</p>
                <ul>
                  <li>
                    <a>comments {{fullPost.comments}}</a>
                  </li>
                  <li>
                    <a>likes{{fullPost.likes}}</a>
                  </li>
                </ul>
              </div>
              <div v-show="postComments.length" class="comments">
                <div v-for="(comment, index) in postComments" :key="index" class="comment">
                  <p>{{comment.userName}}</p>
                  <span>{{comment.createdOn | formatDate}}</span>
                  <p>{{comment.content}}</p>
                </div>
              </div>
            </div>
          </div>
        </transition>

        <!-- 弹窗提示modal -->
        <transition name="fade">
          <div v-if="performingRequest" class="loading">
            <p>{{textTip}}</p>
          </div>
        </transition>

        <!-- post 列表 -->
        <div v-if="posts.length">
          <div v-for="post in posts" class="post">
            <!-- <h5>{{ post.userName }}</h5> -->
            <span>{{ post.userName }} - {{ post.createOn | formatDate }}</span>
            <!-- <span>{{post}}</span> -->
            <p>{{ post.content | trimLength }}</p>
            <ul>
              <li>
                <a @click="openCommentModal(post)">comments {{ post.comments }}</a>
              </li>
              <li>
                <a @click="likePost(post.id,post.likes)">likes {{ post.likes }}</a>
              </li>
              <li>
                <a @click="viewPost(post)">view full post</a>
              </li>
            </ul>
          </div>
        </div>
        <div v-else>
          <p class="no-results">There are currently no posts</p>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
  import {
    mapState
  } from 'vuex'
  import moment from 'moment'

  const fb = require('./firebaseConfig.js')

  export default {
    name: 'Dashboard',
    data() {
      return {
        post: {
          content: ''
        },
        comment: {
          postId: '',
          userId: '',
          content: '',
          postComments: 0
        },
        showCommentModal: false,
        showPostModal: false,
        fullPost: {},
        postComments: [],
        performingRequest: false,
        textTip: ''
      }
    },
    computed: {
      // 数据调用重点关注
      // 对store里面的对象进行映射，展开调用，其他地方就可以直接使用this进行引用。
      // 比如 映射 this.count 为 store.state.count
      ...mapState(['userProfile', 'currentUser', 'posts', 'hiddenPosts'])
    },
    // 建立格式过滤，通过 moment()来格式化日期，使用文档在http://momentjs.cn/
    // <!-- 在双花括号中 -->
    // {{ message | capitalize }}
    filters: {
      formatDate(val) {
        if (!val) {
          return '-'
        }
        let date = val.toDate()
        // return moment(date).fromNow() // 相对时间，比如 a few seconds ago
        return moment(date).format('DD/MM/YYYY, h:mm')
      },
      trimLength(val) {
        if (val.length < 200) {
          return val
        }
        return `${val.substring(0, 200)}...`
      }
    },
    mounted() {
      // console.log('userProfile的log', this.userProfile, this.userProfile.name)

      // console.log('currentUser.uid =》', fb.auth.currentUser.uid)
    },
    methods: {
      createPost() {
        fb.postsCollection
          .add({
            createOn: new Date(),
            content: this.post.content,
            userId: fb.auth.currentUser.uid,
            userName: this.userProfile.name,
            comments: 0,
            likes: 0
          })
          .then(ref => {
            this.post.content = ''
          })
          .catch(err => {
            console.log(err)
          })
      },
      showNewPosts() {
        let updatedPostsArray = this.hiddenPosts.concat(this.posts)
        // clear hiddenPosts array and update  posts array
        this.$store.commit('setHiddenPosts', null)
        this.$store.commit('setPosts', updatedPostsArray)

        // console.log('点击后的hiddenPosts的数据==> ', this.hiddenPosts)
      },
      openCommentModal(post) {
        this.comment.postId = post.id
        this.comment.userId = post.userId
        this.comment.postComments = post.comments
        this.showCommentModal = true
      },
      closeCommentModal() {
        this.comment.postId = ''
        this.comment.userId = ''
        this.comment.content = ''
        this.showCommentModal = false
      },
      addComment() {
        let postId = this.comment.postId
        let postComments = this.comment.postComments
        fb.commentsCollection
          .add({
            createdOn: new Date(),
            content: this.comment.content,
            postId: postId,
            userId: this.currentUser.uid,
            userName: this.userProfile.name
          })
          .then(doc => {
            fb.postsCollection
              .doc(postId)
              .update({
                comments: postComments + 1
              })
              .then(() => {
                this.closeCommentModal()
              })
          })
          .catch(err => {
            console.log(err)
          })
      },
      likePost(postId, postLikes) {
        let docId = `${this.currentUser.uid}_${postId}`

        console.log('获取到的docId 为 ', docId)

        fb.likesCollection
          .doc(docId)
          .get()
          .then(doc => {
            if (doc.exists) {

              // console.log('已经点击过了')
              this.performingRequest = true
              this.textTip = '已经点击过了'
              setTimeout(() => {
                this.performingRequest = false
              }, 1500);
              return
            }

            fb.likesCollection
              .doc(docId)
              .set({
                postId: postId,
                userId: this.currentUser.uid
              })
              .then(() => {
                // update post likes
                fb.postsCollection.doc(postId).update({
                  // 注意字段名
                  likes: postLikes + 1
                })
              })
          })
          .catch(err => {
            console.log(err)
          })
      },
      viewPost(post) {
        fb.commentsCollection
          .where('postId', '==', post.id)
          .get()
          .then(docs => {
            let commentsArray = []

            docs.forEach(doc => {
              let comment = doc.data()
              comment.id = doc.id
              commentsArray.push(comment)
            })

            this.postComments = commentsArray
            this.fullPost = post
            this.showPostModal = true
          })
          .catch(err => {
            console.log(err)
          })
      },
      closePostModal() {
        this.postComments = []
        this.showPostModal = false
      }
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>


</style>



