import firebase from 'firebase'
import 'firebase/firestore'

const config = {
    apiKey: "AIzaSyBjgARHJHoBFEV7kHnorf_ouuLdZOxTcLo",
    authDomain: "vue-firebase-tutorial-67952.firebaseapp.com",
    databaseURL: "https://vue-firebase-tutorial-67952.firebaseio.com",
    projectId: "vue-firebase-tutorial-67952",
    storageBucket: "vue-firebase-tutorial-67952.appspot.com",
    messagingSenderId: "855826516628"
}

firebase.initializeApp(config)

// firebase utils
const db = firebase.firestore()
const auth = firebase.auth()
const currentUser = auth.currentUser
const storage = firebase.storage()

// date issue fix according to firebase
const settings = {
    timestampsInSnapshots: true
}
db.settings(settings)

// firebase collections
const usersCollection = db.collection('users')
const postsCollection = db.collection('posts')
const commentsCollection = db.collection('comments')
const likesCollection = db.collection('likes')

export {
    db,
    auth,
    currentUser,
    usersCollection,
    postsCollection,
    commentsCollection,
    likesCollection,
    storage
}


