<template>
  <section id="settings">
    <div class="col1">
      <h3>Settings</h3>
      <p>Update your profile</p>

      <transition name="fade">
        <p v-if="showSuccess" class="success">profile updated</p>
      </transition>

      <form @submit.prevent>
        <label for="name">Name</label>
        <input type="text" v-model.trim="name" id="name" :placeholder="userProfile.name" />
        <label for="title">Job Title</label>
        <input type="text" id="title" v-model.trim="title" :placeholder="userProfile.title">

        <button @click="updateProfile" class="button">Update Profile</button>
      </form>

      <!-- <button @click="fetchStorage" class="button">取storage数据</button> -->
      <!-- 文件上传 input type file -->
      <input type="file" @change="onFileChanged" class="button-file">
      <button @click="onUpload" class="button">Upload!</button>
      <img :src="imgUrl" alt="" width="90%">
    </div>
  </section>
</template>

<script>
  import {
    mapState
  } from 'vuex'
  import firebase from 'firebase'

  const fb = require('./firebaseConfig.js')

  export default {
    name: 'Settings',
    data() {
      return {
        name: '',
        title: '',
        showSuccess: false,
        selectedFile: null,
        imgUrl: ''
      }
    },
    computed: {
      ...mapState(['userProfile'])
    },
    methods: {
      updateProfile() {
        this.$store.dispatch('updateProfile', {
          name: this.name !== '' ? this.name : this.userProfile.name,
          title: this.title !== '' ? this.title : this.userProfile.title
        })

        this.name = ''
        this.title = ''
        this.showSuccess = true;
        setTimeout(() => {
          this.showSuccess = false
        }, 2000);
      },
      // 获取file组件里面提交上来的文件信息
      onFileChanged(event) {
        this.selectedFile = event.target.files[0]
        console.log('input type files里面的参数变化:', this.selectedFile)

        console.log('当前登录的用户', fb.auth.currentUser.uid);

      },
      onUpload() {
        // 在上传文件前要创建 引用 refrence
        let storageRef = fb.storage.ref();

        let file = this.selectedFile;

        // Created the file metadata
        let metadata = {
          contentType: 'image/jpeg'
        }

        // 生成随机文件名规则
        // console.log('当前登录的用户', fb.currentUser);

        let fileName = 'hrj' + 'logo_' + fb.auth.currentUser.uid;

        // Upload file and metadata to the object 'images/huarenjielogo.jpg'， put(file,metadata)，此处的metadata可以指定，不指定系统会自动识别
        let uploadTask = storageRef.child('images/' + fileName).put(file)

        // Listen for state changes,errors,and completion of the upload
        uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, (snapshot) => {
          // Get task progress,including the number of bytes uploaded and the total number of bytes to be uploaded
          let progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
          console.log('Upload is' + progress + '% done');
          switch (snapshot.state) {
            case firebase.storage.TaskState.PAUSED:
              console.log('Upload is paused');
              break;
            case firebase.storage.TaskState.RUNNING:
              console.log('Upload is running');
              break;
          }
        }, (error) => {
          console.log(error)
        }, () => {
          //Upload completed successfully , now we can get the download URL
          uploadTask.snapshot.ref.getDownloadURL().then(downloadURL => {
            console.log('File available at ', downloadURL);
            this.imgUrl = downloadURL;
          })
        })
      },
      // 取storage上面的数据
      fetchStorage() {

        // 要上传文件之前，要先创建数据引用 reference
        // Points to the root reference
        let storageRef = fb.storage.ref();
        console.log('storageRef的内容：', storageRef)
        // Points to 'images'
        let imagesRef = storageRef.child('images')
        // Points to 'images/space.jpg'
        // Note that you can use variables to create child values
        let fileName = 'space.jpg'
        let spaceRef = imagesRef.child(fileName)
        // File path is 'images/space.jpg'
        let path = spaceRef.fullPath
        // File path is 'space.jpg'
        let name = spaceRef.name

        // Points to 'images'
        // let imagesRef = spaceRef.parent


      }
    }
  }

</script>

